import React from 'react';

function ProjectCard({ project }) {
  return (
    <div className={`bg-[#0D1321] border border-gray-800 rounded-xl shadow-lg hover:shadow-blue-500/10 transition-all duration-300 transform hover:-translate-y-2 overflow-hidden h-full ${project.comingSoon ? 'opacity-75' : ''}`}>
      <div className="p-6">
        <h3 className="text-xl font-bold mb-3 text-white">{project.title}</h3>
        <p className="text-gray-300 mb-4">{project.description}</p>
        <div className="flex flex-wrap gap-2 mb-4">
          {project.technologies.map((tech, index) => (
            <span key={index} className="bg-blue-500/10 text-blue-300 px-2 py-1 text-xs font-medium border border-blue-500/20 rounded">
              {tech}
            </span>
          ))}
        </div>
        
        {project.comingSoon ? (
          <div className="text-gray-400 font-medium flex items-center">
            <span className="inline-block w-2 h-2 bg-purple-500 rounded-full mr-2 animate-pulse"></span>
            Coming Soon
          </div>
        ) : (
          <a 
            href={project.url || "/#/projects"} 
            target={project.url ? "_blank" : "_self"}
            rel={project.url ? "noopener noreferrer" : ""}
            className="text-blue-400 hover:text-blue-300 font-medium flex items-center bg-transparent border-0 p-0 cursor-pointer transition-colors"
          >
            {project.url ? "View Project" : "Learn more"}
            <svg className="w-4 h-4 ml-1" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3"></path>
            </svg>
          </a>
        )}
      </div>
    </div>
  );
}

export default ProjectCard;